.loginForm {
  width: 400px;
}
.loginForm label{
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  color: #9B9FAA;
  font-weight: 700;
  font-size: 14px;
  letter-spacing: 0.04em;
}
.labelEmail {
  margin-bottom: 40px;
}
.labelPass {
  margin-bottom: 60px;
}

.loginForm input{
  width: 240px;
  border: none;
  font-size: 16px;
  border-bottom: 1px solid #E0E0E0;
  outline: none;
}
.title {
margin-bottom: 55px;
color: #FC842D;
text-transform: uppercase;
}

.loginForm button{
  width: 182px;
  padding: 13px 15px;
  font-size: 14px;
    font-weight: 700;
      color: #FC842D;
        letter-spacing: 0.04em;
    background-color: white;
    border-radius: 30px;
    border:2px solid #FC842D;
    box-shadow: 0px 4px 10px rgba(252, 132, 45, 0.5);
}

button[type='submit']{
  color: white;
  background-color: #FC842D;;
  margin-right: 32px;
  box-shadow: 0px 4px 10px rgba(252, 132, 45, 0.5);
}